import React, { Component } from 'react';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';
import TblMhs from '../../../Components/TabelMhs/TabelMhs'
import {Link} from 'react-router-dom';

export default class Mahasiswa extends Component {
    constructor(props) {
        super(props);
        this.state = {
            no: 1,
            dtMhs: [
                { nim: "175610991", nama: "Michael Paijo", jurusan: "SI" },
                { nim: "175610992", nama: "Sumanto", jurusan: "SI" },
                { nim: "173110991", nama: "Tukul Sulistio", jurusan: "MI" },
            ]
        };
    }

    getDataMahasiswa = () => {

    }

    onAdd = () => {
        console.log('to add page');
    }

    onDelete = (nim) => {
        if (window.confirm("Hapus data " + nim + "?")) {
            const filteredData = this.state.dtMhs.filter(mhs => {
                return mhs.nim !== nim;
            });
            this.setState({ dtMhs: filteredData });
        } else return;
    }

    render() {
        return (
            <div className="animated fadeIn">
                <div className="card">
                    <div className="card-header">
                        Data Mahasiswa
                    </div>
                    <div className="card-body">
                        <div className="row">
                            <div className="col-sm-6"><h5>Contoh CRUD Mahasiswa</h5></div>
                            <div className="col-sm-6 text-right">
                                <Link to="/crud/mahasiswa/create">
                                    <button className="btn btn-outline-success">Tambah</button>
                                </Link>
                            </div>
                        </div>
                        <hr />
                        <TblMhs
                            dtMhs={this.state.dtMhs}
                            onDelete={this.onDelete}
                        />
                        <nav className="text-right">
                            <Pagination>
                                <PaginationItem><PaginationLink previous tag="button">Prev</PaginationLink></PaginationItem>
                                <PaginationItem active>
                                    <PaginationLink tag="button">1</PaginationLink>
                                </PaginationItem>
                                <PaginationItem><PaginationLink tag="button">2</PaginationLink></PaginationItem>
                                <PaginationItem><PaginationLink tag="button">3</PaginationLink></PaginationItem>
                                <PaginationItem><PaginationLink tag="button">4</PaginationLink></PaginationItem>
                                <PaginationItem><PaginationLink next tag="button">Next</PaginationLink></PaginationItem>
                            </Pagination>
                        </nav>
                    </div>
                </div>
            </div>
        );
    }
}