import React, { Component } from 'react';
import { Table, Button } from 'reactstrap';

export default class TabelMhs extends Component {
    
    render() {
        //console.log(this.props);
        return (
            <React.Fragment>
                <Table hover bordered striped responsive size="sm">
                    <thead>
                        <tr>
                            <th width="5%" className="text-center">No</th>
                            <th width="15%" className="text-center">NIM</th>
                            <th width="65%" className="text-center">NAMA</th>
                            <th width="5%" className="text-center">JURUSAN</th>
                            <th width="10%" className="text-center"></th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.props.dtMhs.map((mhs, idx) => {
                                return (
                                    <tr key={mhs.nim}>
                                        <td className="text-center">{idx+1}</td>
                                        <td className="text-center">{mhs.nim}</td>
                                        <td>{mhs.nama}</td>
                                        <td className="text-center">{mhs.jurusan}</td>
                                        <td className="text-center">
                                            <div className="btn-group" role="group" aria-label="Basic example">
                                                <Button className="btn btn-sm btn-outline-success"><span className="cui-magnifying-glass icons"></span></Button>
                                                <Button className="btn btn-sm btn-outline-warning"><i className="cui-pencil icons"></i> </Button>
                                                <Button className="btn btn-sm btn-outline-danger" onClick={() => this.props.onDelete(mhs.nim)}><span className="cui-trash icons"></span></Button>
                                            </div>
                                        </td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </Table>
            </React.Fragment>
        );
    }
}